@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600;700&family=VT323&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bruno+Ace&family=Rubik&family=Source+Sans+Pro:wght@400;600;700&family=VT323&display=swap');

.pokeinfo-wrapper {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.70);

  transition: 0.5s ease-in-out;
}

.pokeinfo {
  text-align: center;
  visibility: visible;
  height: 88%;
  width: 600px;
  font-size: 50px;
  color: white;
  font-size: 20px;
  overflow-y: scroll;
  overflow-x: hidden;
  background-color: white;
  margin: auto;
  top: 9%;
  /*
  left: 50%;
  margin-left: -20%;
  */

  will-change: transform;
  min-width: 300px;
  max-width: 85%;
  margin: 50px auto 0px;
  scroll-behavior: smooth;
}

.pokemon-information-background {
  /* background-image: url('./images/topograph.png'); */
  background-size: contain;
  background-color: transparent;
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.information-form {
  width: 50%;
  height: 100%;
  background:crimson;
  border-bottom: 3px solid gold;
  border-left: 3px solid gold;
  border-right: 3px solid gold;

  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: scroll;
}

@media screen and (max-width: 845px) {
  .information-form {
    width: 100%;
    height: 100%;
  }
}

.information-form::-webkit-scrollbar {
  display: none;
}

.pokeimage-window-panel {
  margin: 0px 0 0 0;
  width: 400px;
  height: 600px;
  background-color: #FFFDD0;
  border-radius: 10px 10px 10px 50px;
  border: black 4px solid;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

@media screen and (max-width: 420px) {
  .pokeimage-window-panel {
    width: 90%;
  }
}

.pokeimage-window-panel .top-lights {
  width: 15px;
  margin: 6px 8px 6px 8px;
}

.upperpanel-wrapper {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.pokeimage-screen {
  width: 85%;
  background-color: darkslateblue;
  border: black 4px solid;
  /* height: 600px; */
  margin: 0 auto 10px auto;
  border-radius: 10px 10px 10px 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.pokeimage-screen_types {
  display: flex;
  flex-direction: row;
  width: 100%;
  /* height: 20%; */
  justify-content: center;
  vertical-align: middle;
}

.poketype {
  border-radius: 10px;
  color: white;
  text-align: center;
  vertical-align: middle;
  /* margin: auto; */
  font-family: 'VT323', monospace;
  font-size: 1rem;
  letter-spacing: 1px;
  text-transform: capitalize;
  color: white;
  padding: 5px 10px 5px 10px;
  margin: 10px 2px;
  border: 1px solid white;
}

.image-gallery {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
  
}

.image-gallery_wrapper {
  text-align: center;
  width: 150px;
}

.image-gallery_background {
  background: rgb(132, 165, 74);
  border-radius: 20px;
  border: 2px solid black;
  height: 150px;
}

.current-sprite {
  width: 100%;
  height: 100%;
  object-fit: contain;
  /* background: rgb(132, 165, 74);
  border-radius: 20px;
  border: 2px solid black; */
}

.gallery-left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 5px;
}

.gallery-left img {
  height: 60px;
  width:100%;
}

.gallery-right {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-right: 5px;

}

.gallery-right img {
  height: 60px;
  width:100%;
}

@media screen and (min-width: 1800px) {
  .gallery-left img {
    height: 130px;
  }

  .gallery-right img {
    height: 130px;
  }

  .image-gallery_background {
    border: 4px black solid;
  }
}

@media screen and (max-width: 400px) {
  .gallery-left img {

  }

  .gallery-right img {

  }
}

.generation-text {
  width: 100%;
  /* height: 25%; */
  color: green;
  font-family: 'VT323', monospace;
  font-size: 3rem;
  font-weight: 100;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.generation-text label {
  color: white;
  font-size: 1.5rem;
  text-transform: capitalize;
  text-align: center;
  margin: 5px auto 0 auto;
}


.flavor-text-wrapper {
  display: flex;
  width: 90%;
  flex-direction: row;
  justify-content:center;
  overflow: visible;
}

.flavor-text {
  width: 100%;
  height: 90px;
  color: green;
  font-family: 'VT323', monospace;
  font-weight: 100;
  display: flex;
  flex-direction: row;
  /* justify-content: center; */
  margin: 5px 4px 5px 4px;
  overflow:auto;
}



.flavor-text label {
  color: greenyellow;
  padding: 0 10px 0px 10px;
  text-align: center;
  vertical-align:middle;
  margin: auto 0;
  font-size: 0.85rem;
  white-space:none;
  overflow:auto;
  /* height: 40%; */
  /* margin: auto auto; */
}

.base-stat-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: left;
  width: 85%;
  margin-bottom: 10px;
}

.stats-label {
  margin-top: 5px;
  display: flex;
  flex-direction: row;
  width: 90%;
  white-space: nowrap;
}

.stats-label hr {
  align-self: center;
  border: 2px solid gold;
  border-radius: 20px;
  margin: 0 8px 0 8px;
  width: 100%;
  height: 0px;
}

.stats-label .stat-label {
  font-family: 'Bruno Ace', cursive;
  color: white;
  font-size: 1.5em;
  letter-spacing: 3px;
  align-self: center;
  font-style: bold;
}

.abilities-wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.habitat-wrapper {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.habitat-screen {
  margin: 20px;
  width: 300px;
  height: 200px;
  background: rgb(0, 63, 0);
  border: 3px solid greenyellow;
  background-image: url("./images/topograph.png");
  background-size: contain;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.habitat-info {
