  flex-direction: column;
  justify-content: center;
}

.habitat-info span {
  margin: 10px auto 0 auto;
  font-family: 'VT323', monospace;
  font-size: 2rem;
  color: greenyellow;
  font-weight: 500;
  background: black;
  padding: 3px 0 3px 3px;
}

.habitat-img {
  margin: 0 auto 3px auto;
  object-fit: contain;
  background: rgb(0, 63, 0);
  border-radius: 20px;
  border: 3px solid gold;
  padding: 10px;
  width: 125px;
  height: 100px;
  filter:sepia(50%);
}

.evolutions-wrapper {
  /* width: 50%; */
  margin: 0 0 10px 0;
}

.damage-info {
  width: 80%;
}

@media screen and (max-width: 420px) {
  .evolutions-wrapper {
    width: 80%;
  }
}

.moves-wrapper {
  margin: 8px 0;
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: left;
}

.base-stat {

}

.underscreen-decor {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.underscreen-light {
  padding: 0 0 5px 4%;
  scale: 1.5;
  margin-bottom: 20px;
  width: 15px;
  height: 15px;
}

.hamburger-decor {
  padding: 0 3% 5px 0 ;
  scale: 1.75;
  width: 20px;
  margin: 6px 8px 6px 8px;
}

@media screen and (min-width: 1800px) {
  .information-form {
    width: 40%;
  }

  .pokeimage-window-panel {
    width: 70%;
    height: 100%;
  }

  .image-gallery_wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
  }

  .image-gallery_background {
    width: 300px;
    height: 300px;
  }

  .image-gallery_background img {
    image-rendering: optimizeQuality;
  }

  .flavor-text label {
   font-size: 1.2rem;
  }

  .stat-label {
    font-size: 4rem;
  }

  .habitat-screen {
    width: 600px;
    height: 400px
  }

  .habitat-img {
    width: 275px;
    height: 275px;
  }
}

.pokedexlights {
  display: flex;
  flex-direction: row;
  justify-content: left;
  width: 100%;
}

.pokelight1 {
  width: 15%;
  height: 100%;
  margin-left: 2.5%;
}

@media screen and (max-width: 450px) {

  .flavor-text-wrapper {
    width: 95%;
  }
  .flavor-text label {
    white-space: break-spaces pre;
    font-size: 0.9rem;
   }
}

@media screen and (max-width: 420px) {
  .pokelight1 {
    width: 60px;
    height: 60px;
  }
