.image-box img {
  position: relative;
  border: solid black 2px;
  border-radius: 20px;
  max-width: 100%;
  max-height: 100%;
  min-width: 25px;
  min-height: 25px;
  overflow: hidden;
  transition: 0.5s;
}

.up-image {
  background: url("./images/up-scroll.png");
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  border: none;
  width: 50%;
  position: relative;
}

.down-image {
  background: url("./images/down-scroll.png");
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  border: none;
  position: relative;
  width: 50%;
}


